:root {
  --blue-color: #00acdc;
}

* {
  box-sizing: border-box;
  text-transform: uppercase;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

body {
  margin: 0;
}

.header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 .5rem 1rem #b1b1b1;
}

.nav {
  height: 60px;
  position: relative;
}

.nav_item {
  margin: 0;
  padding: 0;
}

.nav_link {
  color: #000;
  padding: 23px 0;
  text-decoration: none;
  display: block;
  position: relative;
}

.nav_item:not(:last-child) {
  margin-right: 20px;
}

.nav_list {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav_link:hover, .nav_link.is-active {
  color: var(--blue-color);
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  margin: auto;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

